import { StyleSheet, Text, TouchableOpacity, View, Modal } from "react-native";
import React, { useEffect, useState } from "react";
import ProgressBar from "react-native-progress/Bar";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import { useNavigation } from "@react-navigation/native";


const CardFourBarsCompass = (props) => {
  const {title, points} = props
  const [values, setValues] = useState([]);
  const navigation = useNavigation()


  useEffect(() => {
    valueOfStatus(points);
  }, [points]);

  const valueOfStatus = (value) => {
    switch (value) {
      case 4:
        setValues([1, 1, 1, 1]);
        break;
      case 3:
        setValues([1, 1, 1, 0]);
        break;
      case 2:
        setValues([1, 1, 0, 0]);
        break;
      case 1:
        setValues([1, 0, 0, 0]);
        break;
      default:
        setValues([0, 0, 0]);
        break;
    }
  };

  const goToViewInfo = () => {
    navigation.navigate("CompassMoreInfo", {
      headerTitleText: title
    });
  };

  return (
    <View style={styles.containerAllFourBars}>
      <View style={styles.containerCardFourBars}>
        <View style={styles.containerHeadersFourBars}>
          <Text style={styles.textFourBars}>{title}</Text>
        </View>
        <View style={styles.containerProgressBatNeedSatus}>
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[0]}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[1]}
            width={65}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[2]}
            width={65}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[3]}
            width={65}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
        </View>
        <View style={styles.containerButtonProgressBar}>
            <TouchableOpacity
              style={styles.buttonViewResultProgressBar}
              onPress={goToViewInfo}
            >
              <Icon name="eye" size={20} color={"#415972"} />
              <Text style={styles.textViewResultProgressBar}>
                Ver narrativa
              </Text>
            </TouchableOpacity>
          </View>
      </View>
    </View>
  );
};

export default CardFourBarsCompass;

