import React, { useState, useEffect } from "react";
import {
  View,
  TouchableOpacity,
  StyleSheet,
  Text,
  Alert,
  Linking,
} from "react-native";
import { Camera, CameraType } from "expo-camera";
import { useNavigation } from "@react-navigation/native";
import { getCompassData } from "../../service/test/TestService";
import { cleanJsonCompass } from "../../utils/scripts";
import { BarCodeScanner } from "expo-barcode-scanner";
const ScanQR = () => {
  const [type, setType] = useState(CameraType.back);
  const [hasPermission, setHasPermission] = useState(false);
  const [scannedData, setScannedData] = useState("");

  const navigation = useNavigation();

  useEffect(() => {
    saveCompass();
  }, [scannedData]);

  const saveCompass = async () => {
    const guidCompass = scannedData.split("compass/");
    if (scannedData) {
      try {
        const response = await getCompassData(scannedData);
        const compassValue = response.get_compass2_response.COMPASS;
        const compass = cleanJsonCompass(compassValue);
        navigation.navigate("Compass", {
          guidCompass: guidCompass[1],
          compassData: compass,
        });
      } catch (error) {
        Alert.alert(
          "Ha ocurrido un error",
          "Ocurri un error al escanear el QR, por favor intntelo ms tarde"
        );
        navigation.goBack();
      }
    }
  };

  useEffect(() => {
    (async () => {
      const { status } = await Camera.requestCameraPermissionsAsync();
      if (status === "granted") setHasPermission(true);
      else {
        setHasPermission(false);
        Alert.alert(
          "Los permisos no han sido concedidos.",
          "Reddin necesita acceder a la cmara para escanear los cdigos QR. Habilita los permisos manualmente en la configuracin de la aplicacin.",
          [
            {
              text: "Abrir configuracin",
              onPress: () => Linking.openSettings(),
            },
            {
              text: "Cancelar",
              onPress: () => navigation.goBack(),
              style: "cancel",
            },
          ]
        );
      }
    })();
  }, [navigation]);

  const handleBarCodeScanned = async ({ type, data }) => {
    setScannedData(data);
  };

  if (hasPermission === null || hasPermission === false) return <View />;

  return (
    <View style={styles.containerCamera}>
      <Camera
        style={styles.camera}
        type={type}
        barCodeScannerSettings={{
          barCodeTypes: [BarCodeScanner.Constants.BarCodeType.qr],
        }}
        onBarCodeScanned={handleBarCodeScanned}
      >
        <View style={styles.overlay}>
          <View style={styles.scanContainer}></View>
        </View>
      </Camera>
    </View>
  );
};

const styles = StyleSheet.create({
  containerCamera: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  camera: {
    width: "100%",
    maxWidth: 500,
    height: "100%",
    maxHeight: 500
  },
  overlay: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
  },
  scanContainer: {
    width: "70%",
    aspectRatio: 1,
    borderWidth: 2,
    borderColor: "white",
    alignItems: "center",
    justifyContent: "center",
    marginBottom: 20,
  },
  button: {
    backgroundColor: "#F25455",
    paddingVertical: 10,
    paddingHorizontal: 20,
    borderRadius: 5,
  },
  buttonText: {
    color: "white",
    fontSize: 16,
  },
});

export default ScanQR;
