import { StyleSheet, Text, View, Modal } from "react-native";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import React, { useState, useEffect } from "react";
import { TouchableOpacity } from "react-native";
import { replaceTildes, separateNeedStatus } from "../utils/scripts";
import { ActivityIndicator } from "react-native";

const ModalInfo = (props) => {
  const { title, data, isOpen, onModalClose } = props
  const [list, setList] = useState({});

  useEffect(() => {
    fixNarrative(data);
  }, [data]);

  const fixNarrative = (narrative) => {
    const textWithTildes = replaceTildes(narrative.replace(/__BR__/g, ''))
    const textSeparateStatements = separateNeedStatus(textWithTildes);
    setList(textSeparateStatements);
  };

  const handleCloseModal = () => {
    if (onModalClose) onModalClose();
  };

  return (
    <Modal
      visible={isOpen}
      transparent={true}
      animationType="fade"
      onRequestClose={handleCloseModal}
    >
      <View style={styles.modalContainer}>
        <View style={styles.modal}>
          <View style={styles.headerModal}>
            <View style={styles.containerTitleIcon}>
              <Icon
                style={styles.iconInfo}
                name="information"
                size={25}
                color={"#415972"}
              />
              <Text style={styles.modalText}>{title}</Text>
            </View>

            <TouchableOpacity
              style={styles.modalCloseButton}
              onPress={handleCloseModal}
            >
              <Text style={styles.modalCloseButtonText}>X</Text>
            </TouchableOpacity>
          </View>

          <View>
            {/* {data.subTitle ? (
              <Text style={styles.textSubtitle}>{data.subTitle}</Text>
            ) : (
              <></>
            )} */}
            {list.information ? (
              list.information.map((item, index) => (
                <View key={index} style={styles.inlineTextContainerWithIcon}>
                  <Icon name="circle-medium" size={20} color={"#494A50"} />
                  <Text style={styles.textContentModal}>{item.text}</Text>
                </View>
              ))
            ) : (
              <ActivityIndicator />
            )}
          </View>
        </View>
      </View>
    </Modal>
  );
};

export default ModalInfo;

const styles = StyleSheet.create({
  modalContainer: {
    flex: 1,
    backgroundColor: "rgba(0, 0, 0, 0.5)",
    justifyContent: "center",
    alignItems: "center",
  },
  iconInfo: {
    marginLeft: 5,
  },
  containerTitleIcon: {
    flexDirection: "row",
    alignItems: "center",
  },
  modalCloseButton: {
    borderRadius: 16,
    paddingVertical: 10,
    paddingHorizontal: 20,
    marginLeft: 60,
  },
  inlineTextContainerWithIcon: {
    width: "90%",
    flexDirection: "row",
    marginLeft: "7%",
    marginBottom: 20,
    paddingRight: 20,
  },
  modalCloseButtonText: {
    color: "#494A50",
    fontWeight: "bold",
    fontSize: 20,
  },

  modal: {
    width: "90%",
    backgroundColor: "#C9E0F6",
    borderRadius: 16,
    padding: 10,
    maxWidth: 500,
  },
  textContentModal: {
    letterSpacing: 1,
    color: "#494A50",
    fontSize: 12,
  },

  textSubtitle: {
    letterSpacing: 1,
    color: "#494A50",
    fontSize: 12,
    marginLeft: "7%",
    marginBottom: "3%",
  },
  headerModal: {
    flexDirection: "row",
    justifyContent: "space-between",
    marginBottom: 10,
  },
  modalText: {
    color: "#2C3E50",
    fontSize: 12,
    fontWeight: "700",
    marginLeft: 5,
  },
});
