export const getTestData = async (guid) => {
  let config = {
    method: "get",
    //eas build --platform android --profile preview
    url: createURL([`getDeg3?loc%3Aidioma=Esp&loc%3Aid=${guid}`]),
    headers: {
      Cookie: "SESSIONIDX=gih9luJT6hYGOP9oFmJDqWlPN47goF",
      "Content-Type": "application/json"
      },
  };
  try {
    const response = await axios.request(config);
    return response.data;
  } catch (error) {
    console.log("error: ", error);
  }
};

