export const cleanAndParseData = (dataFromService) => {
  let newJson = JSON.parse(JSON.stringify(dataFromService));

  function cleanString(value) {
    return value
      .replace(/<br><br>/g, `__BR__`)
      .replace(/<\/?[^>]+(>|$)/g, "")
      .replace(/[\u0000-\u001F]+/g, "")
      .trim();
  }
  

  function cleanProperties(obj) {
    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        if (typeof obj[key] === "string") {
          obj[key] = cleanString(obj[key]);
        } else if (typeof obj[key] === "object") {
          cleanProperties(obj[key]);
        }
      }
    }
  }
  cleanProperties(newJson);

  return newJson;
};

