import { StyleSheet, Text, TouchableOpacity, View } from "react-native";
import React, { useEffect, useState } from "react";
import Svg, { Circle, Rect } from "react-native-svg";
import ProgressBar from "react-native-progress/Bar";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import { useNavigation } from "@react-navigation/native";

const CreateValues = ({ color, label, value }) => {
  return (
    <View style={styles.containerValues}>
      <View style={styles.containerStyle}>
        <Svg height="20" width="20">
          <Circle cx="10" cy="10" r="10" fill={color} />
        </Svg>
        <Text style={styles.textStyle}>{label}</Text>
      </View>
      <View style={styles.containerValueStyle}>
        <Text style={styles.textValueStyle}>({value.toFixed(1)} / 4)</Text>
      </View>
    </View>
  );
};

const CardPreferredStyle = (props) => {

  const { orientedStyle, OR, OT, narrative } = props

  const navigation = useNavigation();

  useEffect(() => {
    createRect();
  }, []);

  const [ORValue, setORValue] = useState(0);
  const [OTValue, setOTValue] = useState(0);

  const goToViewInfo = () => {
    navigation.navigate("ViewMoreInfo", {
      headerTitleText: "Estilo preferente",
      narrative: narrative,
    });
  };

  const createRect = () => {
    if (OR <= 1) {
      setORValue(225);
    } else if (OR <= 2) {
      setORValue(150);
    }
    else if (OR <= 3) {
      setORValue(75);
    } else {
      setORValue(0);
    }

    if (OT <= 1) {
      setOTValue(0);
    } else if (OT <= 2) {
      setOTValue(75);
    } else if (OT <= 3) {
      setOTValue(150);
    } else {
      setOTValue(225);
    }
  };

  return (
    <View style={styles.containerAllPreferrendStyle}>
      <View style={styles.containerPreferrendStyle}>
        <Text style={styles.textPreferrendStyle}>Estilo preferente:</Text>
      </View>
      <View style={styles.containerAllAboutCardPreferredStyle}>
        <View style={styles.containerCardPreferredStyle}>
          <View style={styles.containerDividedStyles}>
            <View style={styles.containerTextDedicated}>
              <Text style={styles.textDedicated}>Mercado</Text>
            </View>
            <View style={styles.containerTextextBasicStyleOriented}>
              <Text style={styles.textBasicStyleOriented}>
                ( {orientedStyle} )
              </Text>
            </View>
          </View>
          <CreateValues color="#5ED7BA" label="OR" value={OR} />
          <CreateValues color="#C6D75E" label="OT" value={OT} />
          <View style={styles.cotinerGraphics}>
            <View style={styles.containerGraphicsBar}>
              <View style={styles.containerProgressBarOR}>
                <ProgressBar
                  borderWidth={0}
                  progress={OR / 4}
                  width={300}
                  unfilledColor={"#E8E9F1"}
                  height={10}
                  color={"#5ED7BA"}
                />
              </View>
              <View style={styles.containerSVGraphics}>
                <Svg height="300" width="300">
                  {Array.from({ length: 8 }).map((_, rowIndex) =>
                    Array.from({ length: 8 }).map((_, colIndex) => (
                      <Rect
                        key={`${rowIndex}-${colIndex}`}
                        x={colIndex * 37.5}
                        y={rowIndex * 37.5}
                        width={37.5}
                        height={37.5}
                        fill="transparent"
                        stroke="#E8E9F1"
                      />
                    ))
                  )}
                  <Rect
                    x={OTValue}
                    y={ORValue}
                    width={75}
                    height={75}
                    fill={"#5ED7BA"}
                    opacity={0.5}
                  />
                  <Circle
                    cx={(OT * 300) / 4}
                    cy={300 - (OR * 300) / 4}
                    r="25"
                    fill={"#FFE2E2"}
                  />
                  <Circle
                    cx={(OT * 300) / 4}
                    cy={300 - (OR * 300) / 4}
                    r="12.5"
                    fill={"#F25455"}
                  />
                </Svg>
              </View>
            </View>
            <View style={styles.containerProgressBarOT}>
              <ProgressBar
                borderWidth={0}
                progress={OT / 4}
                width={300}
                unfilledColor={"#E8E9F1"}
                height={10}
                color={"#C6D75E"}
              />
            </View>
          </View>
          <View style={styles.containerButtonPreferredStyle}>
            <TouchableOpacity
              style={styles.buttonViewResultPreferredStyle}
              onPress={goToViewInfo}
            >
              <Icon name="eye" size={20} color={"#415972"} />
              <Text style={styles.textViewResultPreferredStyle}>
                Ver narrativa
              </Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    </View>
  );
};

export default CardPreferredStyle;

