import { StyleSheet, Text, TouchableOpacity, View, Modal } from "react-native";
import React, { useEffect, useRef, useState } from "react";
import { useNavigation } from "@react-navigation/native";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import QRCode from "react-native-qrcode-svg";
import { createURL } from "../utils/enviroment";
import { getUserCacheData } from "../service/user/UserService";

const CardTests = (props) => {
  const { name, date } = props;
  const navigation = useNavigation();
  const [showModal, setShowModal] = useState(false);
  const [qrCodeValue, setQrCodeValue] = useState("");
  const qrCodeRef = useRef();

  useEffect(() => {
    getTestQR();
  }, []);

  const getTestQR = async () => {
    const user = await getUserCacheData();
    setQrCodeValue(
      `https://testm.reddinassessments.com:443/compass/${user.GUID_TEST}`
    );
  };

  const handleCloseModal = () => {
    setShowModal(false);
  };

  const shareQR = () => {
    setShowModal(true);
  };

  const goToTest = () => {
    navigation.push("Tests");
  };

  return (
    <View style={styles.containerCard}>
      <TouchableOpacity style={styles.buttonCard} onPress={goToTest}>
        <View style={styles.containerCardInfo}>
          <View style={styles.containerHeaderCardTest}>
            <View style={styles.containerDiagnosis}>
              <Text style={styles.textDiagnosis}>DIAGNSTICO DEG</Text>
            </View>
            <View style={styles.containerIconStar}>
              <TouchableOpacity onPress={shareQR} style={styles.iconButton}>
                <Icon name="share" size={35} color={"#F25455"} />
              </TouchableOpacity>
            </View>
          </View>
          <Text style={styles.textName}>{name}</Text>
          <Text style={styles.textDate}>{date}</Text>
        </View>
        <Modal
          visible={showModal}
          transparent={true}
          animationType="fade"
          onRequestClose={handleCloseModal}
        >
          <View style={styles.modalContainer}>
            <View style={styles.qrCodeContainer}>
              <QRCode
                value={qrCodeValue}
                size={250}
                color={"#F25455"}
                backgroundColor={"#fff"}
                logoSize={30}
                logoMargin={2}
                logoBackgroundColor={"transparent"}
                getRef={(ref) => (qrCodeRef.current = ref)}
              />
              <TouchableOpacity
                style={styles.modalCloseButton}
                onPress={handleCloseModal}
              >
                <Text style={styles.modalCloseButtonText}>Close</Text>
              </TouchableOpacity>
            </View>
          </View>
        </Modal>
      </TouchableOpacity>
    </View>
  );
};

export default CardTests;

