let allResults = {
  CompassLeadershipCompatibility: 0.0,
  CompassAdaptationSkills: 0.0,
  CompassSpeedResponse: 0.0,
  CompassEffectivenessPressure: 0.0,
};

export const calculateLeadershipCompatibility = (user, compass) => {
  //   Estilo de Liderazgo
  let CompassLeadershipStyle = 0;
  let CompassCapacityAnticipation = 0;
  let CompassLevelOfObjectivity = 0;
  let CompassLeadershipCompatibility = 0;

  let results = {
    CompassLeadershipStyle: 0,
    CompassCapacityAnticipation: 0,
    CompassLevelOfObjectivity: 0,
    CompassLeadershipCompatibility: 0,
  };

  if (user.ESTILO_BASICO_ORIENTADO == compass.ESTILO_BASICO_ORIENTADO) {
    CompassLeadershipStyle = 4;
  } else if (
    user.ESTILO_BASICO_ORIENTADO.split("/")[0] ==
    compass.ESTILO_BASICO_ORIENTADO.split("/")[0]
  ) {
    CompassLeadershipStyle = 3;
  } else if (
    user.ESTILO_BASICO_ORIENTADO.split("/")[1] == "REL" &&
    compass.ESTILO_BASICO_ORIENTADO.split("/")[1] == "INT"
  ) {
    CompassLeadershipStyle = 2;
  } else if (
    user.ESTILO_BASICO_ORIENTADO.split("/")[1] == "DED" &&
    compass.ESTILO_BASICO_ORIENTADO.split("/")[1] == "INT"
  ) {
    CompassLeadershipStyle = 2;
  } else if (
    user.ESTILO_BASICO_ORIENTADO.split("/")[1] == "INT" &&
    compass.ESTILO_BASICO_ORIENTADO.split("/")[1] == "REL"
  ) {
    CompassLeadershipStyle = 2;
  } else if (
    user.ESTILO_BASICO_ORIENTADO.split("/")[1] == "INT" &&
    compass.ESTILO_BASICO_ORIENTADO.split("/")[1] == "DED"
  ) {
    CompassLeadershipStyle = 2;
  } else {
    CompassLeadershipStyle = 1;
  }
  console.log(user.EG, compass.EG);
  //   Nivel de Objetividad
  if (user.EG >= 3 && compass.EG >= 3) {
    CompassLevelOfObjectivity = 4;
  } else if (user.EG >= 2.6 && compass.EG >= 2.6) {
    CompassLevelOfObjectivity = 3;
  } else if (user.EG >= 2.2 || compass.EG >= 2.2) {
    CompassLevelOfObjectivity = 2;
  } else {
    CompassLevelOfObjectivity = 1;
  }

  //   Cultura del trabajo
  if (
    (user.HAB_FOBJ + compass.HAB_FOBJ) / 2 >= 2.2 &&
    (user.HAB_PLAN + compass.HAB_PLAN) / 2 >= 2.2
  ) {
    CompassCapacityAnticipation = 4;
  }
  if (
    (user.HAB_FOBJ + compass.HAB_FOBJ) / 2 >= 2 &&
    (user.HAB_PLAN + compass.HAB_PLAN) / 2 >= 2
  ) {
    CompassCapacityAnticipation = 3;
  } else if (
    (user.HAB_FOBJ + compass.HAB_FOBJ) / 2 >= 1.8 &&
    (user.HAB_PLAN + compass.HAB_PLAN) / 2 >= 1.8
  ) {
    CompassCapacityAnticipation = 2;
  } else {
    CompassCapacityAnticipation = 1;
  }

  //compassCompatibilidadDeLiderazgo = ((CompassEstiloDeLiderazgo  .25) + (CompassNivelDeObjetividad  .35) + (CompassCapacidadDeAnticipacion) * .40).toFixed(2);

  CompassLeadershipCompatibility = (
    CompassLeadershipStyle * 0.25 +
    CompassLevelOfObjectivity * 0.35 +
    CompassCapacityAnticipation * 0.4
  ).toFixed(2);

  results.CompassLeadershipStyle = CompassLeadershipStyle;
  results.CompassLevelOfObjectivity = CompassLevelOfObjectivity;
  results.CompassCapacityAnticipation = CompassCapacityAnticipation;
  results.CompassLeadershipCompatibility = parseFloat(
    CompassLeadershipCompatibility
  );

  allResults.CompassLeadershipCompatibility = parseFloat(
    CompassLeadershipCompatibility
  );

  return results;
};

export const calculateAdaptationSkills = (user, compass) => {
  let CompassRelationshipSkills = 0;
  let CompassToExecute = 0;
  let CompassWorkingInGroup = 0;
  let CompassAdaptationSkills = 0;

  let results = {
    CompassRelationshipSkills: 0,
    CompassToExecute: 0,
    CompassWorkingInGroup: 0,
    CompassAdaptationSkills: 0,
  };

  //  Habilidades para relacionarse
  if (
    (user.HAB_COMU + compass.HAB_COMU) / 2 >= 2.2 &&
    (user.HAB_MERR + compass.HAB_MERR) / 2 >= 2.2
  ) {
    CompassRelationshipSkills = 4;
  }
  if (
    (user.HAB_COMU + compass.HAB_COMU) / 2 >= 2 &&
    (user.HAB_MERR + compass.HAB_MERR) / 2 >= 2
  ) {
    CompassRelationshipSkills = 3;
  } else if (
    (user.HAB_COMU + compass.HAB_COMU) / 2 >= 1.8 &&
    (user.HAB_MERR + compass.HAB_MERR) / 2 >= 1.8
  ) {
    CompassRelationshipSkills = 2;
  } else {
    CompassRelationshipSkills = 1;
  }

  //  Para Ejecutar
  if (user.HAB_IMPL >= 2.2 && compass.HAB_IMPL >= 2.2) {
    CompassToExecute = 4;
  } else if (user.HAB_IMPL >= 2 && compass.HAB_IMPL >= 2) {
    CompassToExecute = 3;
  }
  // Corregir para que si uno de los dos cualquiera es menor o igual a 1.8 y el otro es mayor a 1.8 entoces...  Corregir los demas como este.
  else if (user.HAB_IMPL <= 1.8 || compass.HAB_IMPL <= 1.8) {
    CompassToExecute = 2;
  } else {
    CompassToExecute = 1;
  }

  //  Trabajar en Grupo
  if (user.HAB_TEQU >= 2.2 && compass.HAB_TEQU >= 2.2) {
    CompassWorkingInGroup = 4;
  } else if (user.HAB_TEQU >= 2 && compass.HAB_TEQU >= 2) {
    CompassWorkingInGroup = 3;
  } else if (user.HAB_TEQU <= 1.8 || compass.HAB_TEQU <= 1.8) {
    CompassWorkingInGroup = 2;
  } else {
    CompassWorkingInGroup = 1;
  }

  CompassAdaptationSkills = (
    (CompassRelationshipSkills + CompassToExecute + CompassWorkingInGroup) /
    3
  ).toFixed(2);

  results.CompassAdaptationSkills = parseFloat(CompassAdaptationSkills);
  results.CompassRelationshipSkills = CompassRelationshipSkills;
  results.CompassToExecute = CompassToExecute;
  results.CompassWorkingInGroup = CompassWorkingInGroup;

  allResults.CompassAdaptationSkills = parseFloat(CompassAdaptationSkills);

  return results;
};

export const calculateResponseSpeed = (user, compass) => {
  let CompassSenseOfOpportunity = 0;
  let CompassSpeedResponseBar = 0;
  let CompassImpactEvaluation = 0;
  let CompassSpeedResponse = 0;

  let results = {
    CompassSenseOfOpportunity: 0,
    CompassSpeedResponseBar: 0,
    CompassImpactEvaluation: 0,
    CompassSpeedResponse: 0,
  };

  if (
    user.TIPO_REPERTORIO == "Flexibilidad" &&
    compass.TIPO_REPERTORIO == "Flexibilidad" &&
    user.EG >= 1.6 &&
    compass.EG >= 1.6
  ) {
    CompassSenseOfOpportunity = 4;
  } else if (
    user.TIPO_REPERTORIO == "Elasticidad" &&
    compass.TIPO_REPERTORIO == "Elasticidad" &&
    user.EG >= 1.6 &&
    compass.EG >= 1.6
  ) {
    CompassSenseOfOpportunity = 3;
  } else if (user.TIPO_REPERTORIO == "Flexibilidad" && user.EG >= 1.6) {
    CompassSenseOfOpportunity = 2;
  } else if (user.TIPO_REPERTORIO == "Elasticidad" && user.EG >= 1.6) {
    CompassSenseOfOpportunity = 2;
  } else if (compass.TIPO_REPERTORIO == "Flexibilidad" && compass.EG >= 1.6) {
    CompassSenseOfOpportunity = 2;
  } else if (compass.TIPO_REPERTORIO == "Elasticidad" && compass.EG >= 1.6) {
    CompassSenseOfOpportunity = 2;
  } else {
    CompassSenseOfOpportunity = 1;
  }

  //  Velocidad de Respuesta
  if (user.HAB_PROD >= 2.2 && compass.HAB_PROD >= 2.2) {
    CompassSpeedResponseBar = 4;
  } else if (user.HAB_PROD >= 2 && compass.HAB_PROD >= 2) {
    CompassSpeedResponseBar = 3;
  } else if (user.HAB_PROD <= 1.8 || compass.HAB_PROD <= 1.8) {
    CompassSpeedResponseBar = 2;
  } else {
    CompassSpeedResponseBar = 1;
  }

  //  Evaluacin de Impacto
  if (user.HAB_EVAL >= 2.2 && compass.HAB_EVAL >= 2.2) {
    CompassImpactEvaluation = 4;
  } else if (user.HAB_EVAL >= 2 && compass.HAB_EVAL >= 2) {
    CompassImpactEvaluation = 3;
  } else if (user.HAB_EVAL <= 1.8 || compass.HAB_EVAL <= 1.8) {
    CompassImpactEvaluation = 2;
  } else {
    CompassImpactEvaluation = 1;
  }

  CompassSpeedResponse = (
    CompassSenseOfOpportunity * 0.4 +
    CompassSpeedResponseBar * 0.3 +
    CompassImpactEvaluation * 0.3
  ).toFixed(1);

  results.CompassSenseOfOpportunity = CompassSenseOfOpportunity;
  results.CompassSpeedResponseBar = CompassSpeedResponseBar;
  results.CompassImpactEvaluation = CompassImpactEvaluation;
  results.CompassSpeedResponse = parseFloat(CompassSpeedResponse);

  allResults.CompassSpeedResponse = parseFloat(CompassSpeedResponse);

  return results;
};

export const calculateEffectivenessAgainstPressure = (user, compass) => {
  let CompassResilience = 0;
  let CompassEmotionalIntelligence = 0;
  let CompassAutonomyToMakeDecisions = 0;
  let CompassEffectivenessPressure = 0;

  let results = {
    CompassResilience: 0,
    CompassEmotionalIntelligence: 0,
    CompassAutonomyToMakeDecisions: 0,
    CompassEffectivenessPressure: 0,
  };

  //  Resiliencia
  if (user.EAP >= 2.2 && compass.EAP >= 2.2) {
    CompassResilience = 4;
  } else if (user.EAP >= 2 && compass.EAP >= 2) {
    CompassResilience = 3;
  } else if (user.EAP >= 2.2 || compass.EAP >= 2.2) {
    CompassResilience = 2;
  } else {
    CompassResilience = 1;
  }

  //  Inteligencia Emocional
  if (user.ADAPTABILIDAD >= 2.2 && compass.ADAPTABILIDAD >= 2.2) {
    CompassEmotionalIntelligence = 4;
  } else if (user.ADAPTABILIDAD >= 2 && compass.ADAPTABILIDAD >= 2) {
    CompassEmotionalIntelligence = 3;
  } else if (user.ADAPTABILIDAD <= 1.8 || compass.ADAPTABILIDAD <= 1.8) {
    CompassEmotionalIntelligence = 2;
  } else {
    CompassEmotionalIntelligence = 1;
  }

  //  Autonoma para tomar Decisiones

  let userDEG = 4 - user.AUTONOMIA;
  if (userDEG <= 1.8 && compass.AUTONOMIA <= 1.8) {
    CompassAutonomyToMakeDecisions = 4;
  } else if (userDEG <= 2.2 && compass.AUTONOMIA <= 2.2) {
    CompassAutonomyToMakeDecisions = 3;
  } else if (userDEG >= 2.6 || compass.AUTONOMIA >= 2.6) {
    CompassAutonomyToMakeDecisions = 2;
  } else {
    CompassAutonomyToMakeDecisions = 1;
  }

  CompassEffectivenessPressure = (
    (CompassResilience +
      CompassEmotionalIntelligence +
      CompassAutonomyToMakeDecisions) /
    3
  ).toFixed(2);

  results.CompassResilience = CompassResilience;
  results.CompassEmotionalIntelligence = CompassEmotionalIntelligence;
  results.CompassAutonomyToMakeDecisions = CompassAutonomyToMakeDecisions;
  results.CompassEffectivenessPressure = parseFloat(
    CompassEffectivenessPressure
  );
  allResults.CompassEffectivenessPressure = parseFloat(
    CompassEffectivenessPressure
  );

  return results;
};

export const calculateCompatibilityIndex = () => {
  let compassTotal = 0.0;
  compassTotal = (
    allResults.CompassLeadershipCompatibility * 0.2 +
    allResults.CompassAdaptationSkills * 0.3 +
    allResults.CompassSpeedResponse * 0.25 +
    allResults.CompassEffectivenessPressure * 0.25
  ).toFixed(2);

  return compassTotal;
};
