import { StyleSheet, Text, View, ScrollView } from "react-native";
import React, { useEffect, useState } from "react";
import { getTestCacheData } from "../../service/test/TestService";
import CardGaugeCompass from "../../components/cards/CardGaugeCompass";
import CardFourBars from "../../components/cards/CardFourBars";
import CardFourBarsCompass from "../../components/cards/CardFourBarsCompass";
import CardGauge from "../../components/cards/CardGauge";
import {
  calculateAdaptationSkills,
  calculateCompatibilityIndex,
  calculateEffectivenessAgainstPressure,
  calculateLeadershipCompatibility,
  calculateResponseSpeed,
} from "../../utils/calculations";
import { separateByCapitalLetters, separateByColon } from "../../utils/scripts";
import AsyncStorage from "@react-native-async-storage/async-storage";

const CompassResults = ({ route }) => {
  const { compass } = route.params;

  const [userTestData, setUserTestData] = useState({});
  const [compassTestData, setCompassTestData] = useState({});
  const [leadershipCompatibility, setLeadershipCompatibility] = useState({});
  const [adaptationSkills, setAdaptationSkills] = useState({});
  const [speedResponse, setSpeedResponse] = useState({});
  const [effectivenessAgainstPressure, setEffectivenessAgainstPressure] =
    useState({});
  const [compassTotal, setCompassTotal] = useState(0.0);

  useEffect(() => {}, [
    leadershipCompatibility,
    adaptationSkills,
    speedResponse,
    effectivenessAgainstPressure,
    compassTotal,
  ]);

  useEffect(() => {
    try {
      const fetchData = async () => {
        const mainUser = await getTestCacheData();
        const compassUser = compass;
        setUserTestData(mainUser);
        setCompassTestData(compassUser);

        const leadership = calculateLeadershipCompatibility(
          mainUser,
          compassUser
        );
        setLeadershipCompatibility(leadership);

        const skills = calculateAdaptationSkills(mainUser, compassUser);
        setAdaptationSkills(skills);

        const speed = calculateResponseSpeed(mainUser, compassUser);
        setSpeedResponse(speed);

        const effectiveness = calculateEffectivenessAgainstPressure(
          mainUser,
          compassUser
        );
        setEffectivenessAgainstPressure(effectiveness);

        const total = calculateCompatibilityIndex();
        setCompassTotal(parseFloat(total));
      };
      fetchData();
    } catch (error) {
      Alert.alert("Error al pedir los datos", "Intntelo de nuevo ms tarde");
    }
  }, [compass]);

  if (userTestData == {} && compassTestData == {}) {
    return (
      <View style={styles.loadingContainer}>
        <Text>Cargando datos...</Text>
      </View>
    );
  }

  return (
    <View style={styles.containerAllCompassResults}>
      <ScrollView contentContainerStyle={styles.scrollContainerCompassResults}>
        <View style={styles.container}>
          <View style={styles.containerTitleResults}>
            <Text style={styles.titleResultsComparison}>
              Comparacin de tests entre:
            </Text>
            <Text style={styles.namesResultsComparison}>
              {userTestData.NOMBRE} Y {compassTestData.NOMBRE}
            </Text>
          </View>
          <View style={styles.containerAllGraphics}>
            <CardGaugeCompass
              aboutCard={"ndice de compatibilidad"}
              points={compassTotal}
              color={"#39C9C0"}
            />

            <CardGaugeCompass
              aboutCard={"Filosofa de liderazgo"}
              points={leadershipCompatibility.CompassLeadershipCompatibility}
              color={"#4b5b6a"}
            />
            <CardFourBarsCompass
              title={"Preferencia en el liderazgo"}
              points={leadershipCompatibility.CompassLeadershipStyle}
            />
            <CardFourBarsCompass
              title={"Efectividad"}
              points={leadershipCompatibility.CompassLevelOfObjectivity}
            />
            <CardFourBarsCompass
              title={"Cultura de trabajo"}
              points={leadershipCompatibility.CompassCapacityAnticipation}
            />

            <CardGaugeCompass
              aboutCard={"Aptitud gerencial"}
              points={adaptationSkills.CompassAdaptationSkills}
              color={"#4b5b6a"}
            />
            <CardFourBarsCompass
              title={"Habilidades para relacionarse"}
              points={adaptationSkills.CompassRelationshipSkills}
            />
            <CardFourBarsCompass
              title={"Habilidades para ejecutar"}
              points={adaptationSkills.CompassToExecute}
            />
            <CardFourBarsCompass
              title={"Habilidades para trabajar en equipo"}
              narrative={compass.HPTEEINFO}
              points={adaptationSkills.CompassWorkingInGroup}
            />

            <CardGaugeCompass
              aboutCard={"Criterio de respuesta a los dems"}
              infoCard={compass.CDRALDINFO}
              points={speedResponse.CompassSpeedResponse}
              color={"#4b5b6a"}
            />
            <CardFourBarsCompass
              title={"Sentido de oportunidad"}
              points={speedResponse.CompassSenseOfOpportunity}
            />
            <CardFourBarsCompass
              title={"Productividad"}
              points={speedResponse.CompassSpeedResponseBar}
            />
            <CardFourBarsCompass
              title={"Evaluacin"}
              points={speedResponse.CompassImpactEvaluation}
            />

            <CardGaugeCompass
              aboutCard={"Resiliencia"}
              infoCard={compass.RINFO}
              points={effectivenessAgainstPressure.CompassEffectivenessPressure}
              color={"#4b5b6a"}
            />
            <CardFourBarsCompass
              title={"Manejo de la presin"}
              narrative={compass.MDLPINFO}
              points={effectivenessAgainstPressure.CompassResilience}
            />
            <CardFourBarsCompass
              title={"Adaptabilidad"}
              narrative={compass.AINFO}
              points={effectivenessAgainstPressure.CompassEmotionalIntelligence}
            />
            <CardFourBarsCompass
              title={"Facilidad para tomar decisiones"}
              narrative={compass.FPTDINFO}
              points={
                effectivenessAgainstPressure.CompassAutonomyToMakeDecisions
              }
            />
          </View>
        </View>
      </ScrollView>
    </View>
  );
};

export default CompassResults;

const styles = StyleSheet.create({
  loadingContainer: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#fff",
  },
  container: {
    width: "100%",
    maxWidth: 500
  },
  containerAllCompassResults: {
    flex: 1,
    backgroundColor: "#fff",
    display: "flex",
  },
  scrollContainerCompassResults: {
    backgroundColor: "#fff",
    flexGrow: 1,
    paddingTop: 20,
    width: "100%",
    alignItems: "center",
  },
  titleResultsComparison: {
    color: "#2C3E50",
    fontSize: 14,
    fontWeight: "800",
    marginTop: 10,
  },
  namesResultsComparison: {
    color: "#2C3E50",
    fontWeight: "500",
    fontSize: 12,
    marginBottom: 20,
    marginTop: 10,
  },
  containerTitleResults: {
    display: "flex",
    justifyContent: "flex-start",
    marginLeft: 20,
  },
});
