import { StyleSheet, Text, View, TouchableOpacity } from "react-native";
import React from "react";
import ProgressBar from "react-native-progress/Bar";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import { useNavigation } from "@react-navigation/native";

const CardProgressBar = (props) => {
  const { aboutCard, infoCard, points, narrative } = props;

  const navigation = useNavigation();

  const goToViewInfo = () => {
    navigation.navigate("ViewMoreInfo", {
      headerTitleText: aboutCard,
      narrative: narrative,
    });
  };

  return (
    <View style={styles.containerAllProgressBar}>
      <View style={styles.containerInfoAboutProgressBar}>
        <Text style={styles.textInfoAboutProgressBar}>{aboutCard}:</Text>
      </View>
      <View style={styles.containerAllCardProgressBar}>
        <View style={styles.containerCardProgressBar}>
          <View style={styles.containerHeadersProgressBar}>
            <Text style={styles.textProgressBar}>{infoCard}</Text>
            <View style={styles.containerInfoProgressBar}>
              <Text style={styles.textType}>(+{points}/4)</Text>
            </View>
          </View>
          <View style={styles.containerProgressBarAll}>
            <ProgressBar
              //style={styles.progressBarProgressBar}
              borderWidth={0}
              progress={points / 4}
              width={null}
              unfilledColor={"#E8E9F1"}
              height={10}
              color={"#F25455"}
            />
          </View>
          <View style={styles.containerButtonProgressBar}>
            <TouchableOpacity
              style={styles.buttonViewResultProgressBar}
              onPress={goToViewInfo}
            >
              <Icon name="eye" size={20} color={"#415972"} />
              <Text style={styles.textViewResultProgressBar}>
                Ver narrativa
              </Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    </View>
  );
};

export default CardProgressBar;

