import { StyleSheet, Text, View, TouchableOpacity } from "react-native";
import React from "react";
import ProgressBar from "react-native-progress/Bar";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import { useNavigation } from "@react-navigation/native";

const CardDividedBar = (props) => {
  const {
    title,
    aboutOne,
    scoreOne,
    aboutTwo,
    scoreTwo,
    scoreVisible,
    narrative,
  } = props;
  const navigation = useNavigation();

  const goToViewInfo = () => {
    navigation.navigate("ViewMoreInfo", {
      headerTitleText: title,
      narrative: narrative,
    });
  };

  return (
    <View style={styles.containerAllDividedBar}>
      <View style={styles.containerInfoAboutDividedBar}>
        <Text style={styles.textInfoAboutDividedBar}>{title}</Text>
      </View>
      <View style={styles.containerAllCardDividedBar}>
        <View style={styles.containerCardDividedBar}>
          <View style={styles.containerHeadersDividedBar}>
            <Text style={styles.textDividedBar}>{aboutOne}</Text>
            <Text style={styles.textScoreDividedBar}>
              {scoreVisible ? scoreOne : ""}
            </Text>
            <View style={styles.containerInfoDividedBar}>
              <Text style={styles.textScoreDividedBar}>
                {scoreVisible ? scoreTwo : ""}
              </Text>
              <Text style={styles.textDividedBar}>{aboutTwo}</Text>
            </View>
          </View>
          <View style={styles.containerDividedBarAll}>
            <ProgressBar
              style={styles.DividedBarDividedBarReverse}
              borderWidth={0}
              progress={scoreOne / 4}
              width={150}
              unfilledColor={"#E8E9F1"}
              height={10}
              color={"#F25455"}
            />
            <ProgressBar
              style={styles.DividedBarDividedBar}
              borderWidth={0}
              progress={scoreTwo / 4}
              width={150}
              unfilledColor={"#E8E9F1"}
              height={10}
              color={"#F25455"}
            />
          </View>
          {narrative ? (
            <View style={styles.containerButtonProgressBar}>
              <TouchableOpacity
                style={styles.buttonViewResultProgressBar}
                onPress={goToViewInfo}
              >
                <Icon name="eye" size={20} color={"#415972"} />
                <Text style={styles.textViewResultProgressBar}>
                  Ver narrativa
                </Text>
              </TouchableOpacity>
            </View>
          ) : (
            <></>
          )}
        </View>
      </View>
    </View>
  );
};

export default CardDividedBar;

