import { StyleSheet, Text, TouchableOpacity, View, Modal } from "react-native";
import React, { useEffect, useState } from "react";
import ProgressBar from "react-native-progress/Bar";

const CardFourBars = (props) => {
  const { title, points} = props
  const [values, setValues] = useState([]);


  useEffect(() => {
    valueOfStatus(points);
  }, [points]);

  const valueOfStatus = (value) => {
    switch (value) {
      case 4:
        setValues([1, 1, 1, 1]);
        break;
      case 3:
        setValues([1, 1, 1, 0]);
        break;
      case 2:
        setValues([1, 1, 0, 0]);
        break;
      case 1:
        setValues([1, 0, 0, 0]);
        break;
      default:
        setValues([0, 0, 0]);
        break;
    }
  };

  return (
    <View style={styles.containerAllFourBars}>
      <View style={styles.containerCardFourBars}>
        <View style={styles.containerHeadersFourBars}>
          <Text style={styles.textFourBars}>{title}:</Text>
        </View>
        <View style={styles.containerProgressBatNeedSatus}>
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[0]}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[1]}
            width={65}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[2]}
            width={65}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
          <ProgressBar
            style={styles.progressBarFourBars}
            borderWidth={0}
            progress={values[3]}
            width={65}
            unfilledColor={"#E8E9F1"}
            height={10}
            color={"#39C9C0"}
          />
        </View>
      </View>
    </View>
  );
};

export default CardFourBars;

