import React, { useState, useEffect } from "react";
import {
  View,
  Text,
  StyleSheet,
  Image,
  TouchableOpacity,
  SafeAreaView,
  BackHandler,
} from "react-native";
import logo from "../../assets/img/ReddinAssessmentsLogo.png";
import { useNavigation } from "@react-navigation/native";
import MaterialCommunityIcons from "react-native-vector-icons/MaterialCommunityIcons";
import {
  getCredentialsCacheData,
  getUserCacheData,
} from "../../service/user/UserService";

const Home = () => {
  const navigation = useNavigation();
  const [dataUser, setDataUser] = useState({});
  const [credentials, setCredentials] = useState({});

  useEffect(() => {
    const userData = getUserCacheData().then((data) => setDataUser(data));
    const userCredentials = getCredentialsCacheData().then((data) =>
      setCredentials(data)
    );
  }, []);

  useEffect(() => {
    const backHandler = BackHandler.addEventListener(
      "hardwareBackPress",
      () => {
        if (navigation.isFocused()) {
          BackHandler.exitApp();
          return true;
        }
        return false;
      }
    );

    return () => {
      backHandler.remove();
    };
  }, [navigation]);

  const goToBrowseTests = () => {
    navigation.push("BrowseTests");
  };

  const goToCompass = () => {
    navigation.push("Compass");
  };

  return (
    <SafeAreaView style={styles.contentAllHomeMenu}>
      <View style={styles.containerHeader}>
        <View style={styles.header}>
          <Image source={logo} style={styles.logoReddinHome} />
        </View>
        <View style={styles.containerInfoUser}>
          <View style={styles.initialsUserHome}>
            <Text style={styles.textInitials}>{dataUser.INITIALS}</Text>
          </View>
          <View style={styles.containerUserName}>
            <Text style={styles.nameUserHome}>{dataUser.USERNAME}</Text>
          </View>
        </View>
        <View style={styles.textsHome}>
          <Text style={styles.textInfo}>Login: {credentials.username}</Text>
        </View>
      </View>
      <View style={styles.optionsHome}>
        <TouchableOpacity
          onPress={goToBrowseTests}
          style={styles.containerOptionsHome}
        >
          <Text style={styles.textsOptions}>My tests</Text>
          <MaterialCommunityIcons
            style={styles.imageIconsHome}
            name={"view-grid"}
            size={60}
            color={"#F25455"}
          />
        </TouchableOpacity>
        <TouchableOpacity
          onPress={goToCompass}
          style={styles.containerOptionsHome}
        >
          <Text style={styles.textsOptions}>Compass</Text>
          <MaterialCommunityIcons
            style={styles.imageIconsHome}
            name={"compass-outline"}
            size={60}
            color={"#F25455"}
          />
        </TouchableOpacity>
      </View>
    </SafeAreaView>
  );
};

