import { ScrollView, StyleSheet, Text, View } from "react-native";
import React, { useEffect, useState } from "react";
import { replaceTildes } from "../../utils/scripts";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import { TYPES } from "../../utils/types";
import {
  APTITUD_GERENCIAL,
  COMPASS_ADAPTABILIDAD,
  COMPASS_EVALUACION,
  COMPASS_PRODUCTIVIDAD,
  COMPATIBILIDAD_FILOSOFIA_LIDERAZGO,
  CRITERIO_RESPUESTA,
  CULTURA_TRABAJO,
  EFECTIVIDAD,
  FACILIDAD_TOMAR_DECISIONES,
  HABILIDADES_EJECUTAR,
  HABILIDADES_RELACIONARSE,
  HABILIDADES_TRABAJAR_EQUIPO,
  INDICE_COMPATIBILIDAD,
  MANEJO_PRESION,
  PREFERENCIA_LIDERAZGO,
  RESILIENCIA,
  SENTIDO_OPORTUNIDAD,
} from "../../utils/texts";

const CompassMoreInfo = ({ route }) => {
  const { headerTitleText } = route.params;
  const type = headerTitleText;

  const [narrative, setNarrative] = useState(null);
  useEffect(() => {
    mapData();
  }, []);

  const mapData = async () => {
    switch (type) {
      case TYPES.INDICE_COMPATIBILIDAD:
        setNarrative(INDICE_COMPATIBILIDAD);
        break;
      case TYPES.COMPATIBILIDAD_FILOSOFIA_LIDERAZGO:
        setNarrative(COMPATIBILIDAD_FILOSOFIA_LIDERAZGO);
        break;
      case TYPES.PREFERENCIA_LIDERAZGO:
        setNarrative(PREFERENCIA_LIDERAZGO);
        break;
      case TYPES.EFECTIVIDAD:
        setNarrative(EFECTIVIDAD);
        break;
      case TYPES.CULTURA_TRABAJO:
        setNarrative(CULTURA_TRABAJO);
        break;
      case TYPES.APTITUD_GERENCIAL:
        setNarrative(APTITUD_GERENCIAL);
        break;
      case TYPES.HABILIDADES_RELACIONARSE:
        setNarrative(HABILIDADES_RELACIONARSE);
        break;
      case TYPES.HABILIDADES_EJECUTAR:
        setNarrative(HABILIDADES_EJECUTAR);
        break;
      case TYPES.HABILIDADES_TRABAJAR_EQUIPO:
        setNarrative(HABILIDADES_TRABAJAR_EQUIPO);
        break;
      case TYPES.CRITERIO_RESPUESTA:
        setNarrative(CRITERIO_RESPUESTA);
        break;
      case TYPES.SENTIDO_OPORTUNIDAD:
        setNarrative(SENTIDO_OPORTUNIDAD);
        break;
      case TYPES.COMPASS_PRODUCTIVIDAD:
        setNarrative(COMPASS_PRODUCTIVIDAD);
        break;
      case TYPES.COMPASS_EVALUACION:
        setNarrative(COMPASS_EVALUACION);
        break;
      case TYPES.RESILIENCIA:
        setNarrative(RESILIENCIA);
        break;
      case TYPES.MANEJO_PRESION:
        setNarrative(MANEJO_PRESION);
        break;
        case TYPES.COMPASS_ADAPTABILIDAD:
        setNarrative(COMPASS_ADAPTABILIDAD);
        break;
        case TYPES.FACILIDAD_TOMAR_DECISIONES:
        setNarrative(FACILIDAD_TOMAR_DECISIONES);
        break;
    }
  };

  useEffect(() => {}, [narrative]);

  if (narrative === null) {
    return (
      <View style={styles.loadingContainer}>
        <Text>Cargando...</Text>
      </View>
    );
  }

  return (
    <View style={styles.containerAll}>
      <ScrollView contentContainerStyle={styles.containerAllInfo}>
        <Text style={styles.textTitle}>{headerTitleText}</Text>
        <View style={styles.hr} />
        {type == TYPES.INDICE_COMPATIBILIDAD ||
        type == TYPES.PREFERENCIA_LIDERAZGO ||
        type == TYPES.APTITUD_GERENCIAL ||
        type == TYPES.RESILIENCIA ||
        type == TYPES.CRITERIO_RESPUESTA ||
        type == TYPES.SENTIDO_OPORTUNIDAD ||
        type == TYPES.EFECTIVIDAD ||
        type == TYPES.COMPASS_ADAPTABILIDAD ||
        type == TYPES.MANEJO_PRESION ||
        type == TYPES.MANEJO_PRESION ||
        type == TYPES.FACILIDAD_TOMAR_DECISIONES ||
        type == TYPES.COMPATIBILIDAD_FILOSOFIA_LIDERAZGO ? (
          <>
            {narrative.information.map((item, index) => {
              return (
                <Text style={styles.textInfo} key={index}>
                  {item.text}
                </Text>
              );
            })}
          </>
        ) : (
          <>
            {narrative.information.map((item, index) => {
              return (
                <Text style={styles.textInfo} key={index}>
                  {item.text}
                </Text>
              );
            })}
            {narrative.list.map((item, index) => {
              return (
                <View key={index} style={styles.inlineTextContainerWithIcon}>
                  <Icon
                    name="circle-medium"
                    size={20}
                    color={"#415972"}
                    style={styles.iconStyle}
                  />
                  <Text style={styles.textInfo} key={index}>
                    {item.text}
                  </Text>
                </View>
              );
            })}
            {narrative.secondaryInformation.map((item, index) => {
              return (
                <Text style={styles.textInfo} key={index}>
                  {item.text}
                </Text>
              );
            })}
            {narrative.secondList.map((item, index) => {
              return (
                <View key={index} style={styles.inlineTextContainerWithIcon}>
                  <Icon
                    name="circle-medium"
                    size={20}
                    color={"#415972"}
                    style={styles.iconStyle}
                  />
                  <Text style={styles.textInfo} key={index}>
                    {item.text}
                  </Text>
                </View>
              );
            })}
          </>
        )}
      </ScrollView>
    </View>
  );
};

export default CompassMoreInfo;

const styles = StyleSheet.create({
  containerAll: {
    backgroundColor: "#fff",
    flex: 1,
  },
  containerAllInfo: {
    backgroundColor: "#fff",
    padding: 10,
    flexGrow: 1,
  },
  textTitle: {
    color: "#F25455",
    fontSize: 14,
    fontWeight: "800",
    marginTop: 10,
    marginBottom: 5,
  },
  hr: {
    borderTopWidth: 1,
    borderColor: "#D4D6DD",
    marginVertical: 20,
    width: "100%",
    height: 1,
    backgroundColor: "#D4D6DD",
    marginVertical: 20,
    alignSelf: "center",
  },
  textInfo: {
    letterSpacing: 0.7,
    fontSize: 13,
    color: "#2C3E50",
    fontWeight: "400",
  },
  iconStyle: {
    marginTop: -5,
    marginRight: 5,
  },
  textInfoContainer: {
    flexDirection: "row",
    flexWrap: "wrap",
  },
  inlineTextContainerWithIcon: {
    width: "90%",
    flexDirection: "row",
  },
  loadingContainer: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
});
