import { StyleSheet, Text, View } from 'react-native'
import React, { useState } from 'react'
import { ListItem } from '@rneui/themed';
import { Icon, } from 'react-native-elements';

const AccordionButtom = (props) => {
    const { typeAccoirdion, contentAccoirdion, bottom } = props
    const [expandedPC, setExpandedPC] = useState(false);

    return (
        <View style={[styles.containerAllAccordion,{paddingBottom: bottom}]}>
            <View style={styles.containerAccoirdion}>
                <ListItem.Accordion
                    content={
                        <>
                            <ListItem.Content>
                                <ListItem.Title style={styles.headerTitleAccordion}>{typeAccoirdion}</ListItem.Title>
                            </ListItem.Content>
                        </>
                    }
                    isExpanded={expandedPC}
                    onPress={() => {
                        setExpandedPC(!expandedPC);
                    }}
                    icon={<Icon name={'chevron-down'} color={"#F25455"} type="material-community" />}
                    containerStyle={styles.accordionSkills}
                >
                    <ListItem bottomDivider containerStyle={styles.listItemSkills}>
                        <Text style={styles.textAboutAccordion}>{contentAccoirdion}</Text>
                    </ListItem>

                </ListItem.Accordion>
            </View>
        </View>
    )
}

export default AccordionButtom

const styles = StyleSheet.create({
    containerAllAccordion: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "#fff",
    },
    headerTitleAccordion: {
        fontWeight: "700",
        color: "#71727A",
        fontSize: 12,
    },
    containerAccoirdion: {
        width: '90%',
        height: "auto",
        backgroundColor: '#F8F9FE',
        marginTop: '5%',
        borderRadius: 12,
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.2,
        shadowRadius: 2,
        elevation: 2,
        marginBottom: 10
    },
    accordionSkills: {
        backgroundColor: "transparent",
    },
    listItemSkills: {
        backgroundColor: "transparent",
        borderWidth: 0,
    },
    textAboutAccordion: {
        textAlign: "left",
        color: "#71727A",
        fontWeight: "400",
        lineHeight: 18,
        fontSize: 12
    },
})