import { StyleSheet, Text, TouchableOpacity, View } from "react-native";
import React from "react";
import ProgressBar from "react-native-progress/Bar";
import { useNavigation } from "@react-navigation/native";

const CreateDoubleProgressBar = (props) => {
  const {info, points, color } = props;
  return (
    <View>
      <View style={styles.containerHeadersProgressBar}>
        <Text style={styles.textProgressBar}>{info}</Text>
        <View style={styles.containerInfoProgressBar}>
          <Text style={styles.textType}>({points}/100)</Text>
        </View>
      </View>
      <View style={styles.containerProgressBarAll}>
        <ProgressBar
          style={styles.progressBarProgressBar}
          borderWidth={0}
          progress={points / 100}
          width={null}
          unfilledColor={"#E8E9F1"}
          height={10}
          color={color}
        />
      </View>
    </View>
  );
};

const CardDoubleProgressBar = ({
  aboutCard,
  infoCardOne,
  infoCardTwo,
  pointsOne,
  pointsTwo,
  color,
}) => {
  const navigation = useNavigation();

  const goToViewInfo = () => {
    navigation.navigate("ViewMoreInfo", {
      headerTitleText: aboutCard,
    });
  };

  return (
    <View style={styles.containerAllProgressBar}>
      {aboutCard != "" ? (
        <View style={styles.containerHeaderDoubleProgressBar}>
          <Text style={styles.textHeaderDoubleProgressBar}>{aboutCard}:</Text>
          <TouchableOpacity onPress={goToViewInfo}>
            <Text style={styles.textMoreInfoHeaderDoubleProgressBar}>
              + info
            </Text>
          </TouchableOpacity>
        </View>
      ) : (
        <View style={{ marginBottom: -10 }}></View>
      )}
      <View style={styles.containerAllCardProgressBar}>
        <View style={styles.containerCardProgressBar}>
          <CreateDoubleProgressBar
            info={infoCardOne}
            points={pointsOne}
            color={color}
          />
          <CreateDoubleProgressBar
            info={infoCardTwo}
            points={pointsTwo}
            color={color}
          />
          <View style={styles.containerButtonProgressBar}>
            <View style={styles.buttonViewResultProgressBar}>
              <Text style={styles.textViewResultProgressBar}>
                Balance total: {pointsTwo - pointsOne}
              </Text>
            </View>
          </View>
        </View>
      </View>
    </View>
  );
};

export default CardDoubleProgressBar;

