import React, { useState } from "react";
import {
  View,
  Image,
  StyleSheet,
  Text,
  TextInput,
  KeyboardAvoidingView,
  Platform,
  TouchableOpacity,
  Alert,
  ActivityIndicator,
  Modal,
  Linking,
} from "react-native";
import backgroundLogin from "../../assets/img/backgroundLogin.png";
import logo from "../../assets/img/ReddinAssessmentsLogo.png";
import MaterialCommunityIcons from "react-native-vector-icons/MaterialCommunityIcons";
import { useNavigation } from "@react-navigation/native";
import { StatusBar } from "expo-status-bar";
import { LoginUser } from "../../service/user/UserService";
import AsyncStorage from "@react-native-async-storage/async-storage";
import {
  cleanAndParseData,
  getTestData,
  getTestDataAndroid,
} from "../../service/test/TestService";
import { replaceTildes } from "../../utils/scripts";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isVisible, setIsVisible] = useState(false);
  const [visible, setVisible] = useState(true);
  const [isLoading, setIsLoading] = useState(false);

  const navigation = useNavigation();

  const handleEmailChange = (text) => {
    setEmail(text);
  };

  const handlePasswordChange = (text) => {
    setPassword(text);
  };

  const togglePasswordVisibility = () => {
    setVisible(!visible);
  };

  const closeModal = () => {
    setIsVisible(false);
  };

  const goToLink = () => {
    Linking.openURL("https://reddinassessments.com/");
  };

  const goToHome = async () => {
    if (!(email == "" || password == "")) {
      setIsLoading(true);
      try {
        const response = await LoginUser(email, password);

        if (response.wsGetMyTests_response.resultComplete.USERNAME == "-1") {
          Alert.alert(
            "Usuario y/o contrasea incorrectos",
            "Introduce credenciales existentes"
          );
        } else {
          console.log(
            "El nombre: ",
            response.wsGetMyTests_response.resultComplete.USERNAME
          );
          const correctName = replaceTildes(
            response.wsGetMyTests_response.resultComplete.USERNAME
          );
          const userData = {
            USERNAME: correctName,
            INITIALS: response.wsGetMyTests_response.resultComplete.INITIALS,
            GUID_TEST: response.wsGetMyTests_response.resultComplete.GUID_TEST,
            GUID: response.wsGetMyTests_response.resultComplete.GUID,
          };

          const credentials = {
            username: email,
            password: password,
          };

          await AsyncStorage.setItem("userData", JSON.stringify(userData));
          await AsyncStorage.setItem(
            "credentials",
            JSON.stringify(credentials)
          );

          try {
            let responseTestData;
            if (Platform.OS === "ios") {
              responseTestData = await getTestData(userData.GUID_TEST);
            } else {
              responseTestData = await getTestDataAndroid(userData.GUID_TEST);
            }

            if (responseTestData && responseTestData.DEG_found === 0) {
              Alert.alert(
                "Usuario sin resultados",
                "No se encontr informacin de diagnsticos relacionada a este usuario"
              );
            } else if (responseTestData) {
              const data = cleanAndParseData(responseTestData);
              const dataParse = JSON.parse(data.DEG);
              await AsyncStorage.setItem("testData", JSON.stringify(dataParse));
              setEmail("");
              setPassword("");
              setVisible(false);
              navigation.navigate("HomeUser");
            } else {
              Alert.alert(
                "Error al conseguir los datos",
                "Intntelo de nuevo ms tarde"
              );
            }
          } catch (error) {
            Alert.alert(
              "Error al conseguir los datos",
              "Intntelo de nuevo ms tarde"
            );
          }
        }
      } catch (error) {
        Alert.alert(
          "Error al conseguir los datos",
          "Intntelo de nuevo ms tarde"
        );
      } finally {
        setIsLoading(false);
      }
    } else Alert.alert("Campos vacos", "Por favor llene todos los campos");
  };

  return (
    <View style={styles.container}>
      <KeyboardAvoidingView
        behavior={Platform.OS === "ios" ? "padding" : "height"}
        style={styles.keyboardContainer}
      >
          <Image source={backgroundLogin} style={styles.backgroundImage} />
        <View style={styles.logoContainer}>
          <Image source={logo} style={styles.logo} />
        </View>
        <View style={styles.containerAllInfo}>
          <View style={styles.textContainer}>
            <Text style={styles.textWelcome}>Welcome!</Text>
          </View>
          <View style={styles.containerForm}>
            <TextInput
              placeholder="User email"
              keyboardType="email-address"
              style={styles.inputForm}
              autoCapitalize="none"
              cursorColor={"#F25455"}
              value={email}
              onChangeText={handleEmailChange}
            />
            <View style={styles.passwordInputContainer}>
              <TextInput
                style={styles.inputForm}
                placeholder="Password"
                secureTextEntry={visible}
                selectionColor={"#F25455"}
                cursorColor={"#F25455"}
                value={password}
                onChangeText={handlePasswordChange}
              />
              <MaterialCommunityIcons
                name={visible ? "eye-off" : "eye"}
                size={20}
                color="#8F9098"
                onPress={togglePasswordVisibility}
                style={styles.passwordIcon}
              />
            </View>
          </View>
          <View style={styles.containerForgotPassword}>
            <TouchableOpacity onPress={() => setIsVisible(true)}>
              <Text style={styles.textForgotPassword}>Forgot Password?</Text>
            </TouchableOpacity>
          </View>
          <View style={styles.containerButtonLogin}>
            <TouchableOpacity style={styles.buttonLogin} onPress={goToHome}>
              <Text style={styles.textButtonLogin}>Login</Text>
            </TouchableOpacity>
          </View>
          <View style={styles.notMember}>
            <Text style={styles.textQuestion}>Not a member?</Text>
            <TouchableOpacity onPress={goToLink}>
              <Text style={styles.textLinkRegister}>Ask for your demo</Text>
            </TouchableOpacity>
          </View>
        </View>
      </KeyboardAvoidingView>
      {isLoading && (
        <View style={styles.loadingContainer}>
          <ActivityIndicator size="large" color="#F25455" />
          <Text style={styles.textLoading}>Loading...</Text>
        </View>
      )}
      <Modal
        transparent={true}
        visible={isVisible}
        animationType="fade"
        onRequestClose={closeModal}
      >
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View style={styles.containerTitleModal}>
              <Text style={styles.titleModal}>Forgotten password</Text>
            </View>
            <Text style={styles.textModalLogout}>
              If you have forgotten your password, please contact the Reddin
              Assessments team to recover it.
            </Text>
            <View style={styles.modalButtons}>
              <TouchableOpacity
                style={styles.modalButtonLogout}
                onPress={closeModal}
              >
                <Text style={styles.textLogout}>OK</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </Modal>
    </View>
  );
};

