import { ScrollView, StyleSheet, Text, View } from "react-native";
import React, { useEffect } from "react";
import {
  EFECTIVIDAD_LIDERAZGO,
  ESTILO_PREFERENTE,
  COMPARATIVA_EFECTIVIDAD,
  RESISTENCIA_CAMBIO,
  COMPARATIVA_RESISTENCIA,
  REPERTORIO_ESTILOS,
  BALANCE_ESTILOS,
  COMPARATIVA_REPERTORIO,
  EFECTIVIDAD_PRESION,
  COMPARATIVA_PRESION,
  NECESIDAD_ESTRUCTURACION,
  COMPARATIVA_ESTRUCTURACION,
  ADAPTABILIDAD,
  COMPARATIVA_ADAPTABILIDAD,
  COMPARATIVA_COMPETENCIAS,
  RELACION_SUPERIORES,
  RELACION_COLEGAS,
  RELACION_EQUIPO,
  RELACION_ASESORES,
  RELACION_GRUPOS,
  RELACION_CLIENTES,
  RELACION_PUBLICO,
  CREATIVIDAD,
  FIJACION_OBJETIVOS,
  PLANEACION,
  MANEJO_CAMBIO,
  IMPLEMENTACION,
  CONTROLES,
  EVALUACION,
  PRODUCTIVIDAD,
  COMUNICACION,
  MANEJO_CONFLICTO,
  MANEJO_ERRORES,
  COMUNICACION_JUNTAS,
  TRABAJO_EQUIPO,
  POTENCIAL_DESARROLLO,
  COMPARATIVA_POTENCIAL,
} from "../../utils/texts";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import { TYPES } from "../../utils/types";
import { useState } from "react";
import { replaceTildes, separateStatements } from "../../utils/scripts";

const ViewMoreInfo = ({ route }) => {
  const [data, setData] = useState(null);
  const [list, setList] = useState({});
  const [textResult, setTextResult] = useState("");
  const [narrativeText, setNarrativeText] = useState("");
  const { headerTitleText, narrative, score, compassNarrative } = route.params;
  const type = headerTitleText;

  useEffect(() => {
    mapData();
  }, []);

  const fixNarrative = (narrative) => {
    const cleanNarrative = narrative.replace(/__BR__/g, "");
    const textWithTildes = replaceTildes(cleanNarrative);
    const textSeparateStatements = separateStatements(textWithTildes);

    setList({
      secondaryInformation: textSeparateStatements,
    });
  };

  function parseText(text) {
    const parts = text
      .split(". ")
      .map((part) => part.trim())
      .filter(Boolean);

    const formattedText = {
      textOne: parts[0].replace(".", "") + ".",
      textTwo: parts[1].replace(".", "") + ":",
      primaryInformation: parts
        .slice(2, 6)
        .map((text) => ({ text: text.replace(".", "") + "." })),
      textThree: parts[6].replace(".", "") + ":",
      secondaryInformation: parts
        .slice(7)
        .map((text) => ({ text: text.replace(".", "") + "." })),
    };

    return formattedText;
  }

  const mapData = async () => {
    if (compassNarrative) {
      if (headerTitleText == "Cultura del trabajo") {
        const response = parseText(compassNarrative);
      } else setData({ title: "Descripcin:", information: compassNarrative });
    } else {
      switch (type) {
        case TYPES.ESTILO_PREFERENTE:
          setData(ESTILO_PREFERENTE);
          fixNarrative(narrative);
          break;
        case TYPES.EFECTIVIDAD_LIDERAZGO:
          setData(EFECTIVIDAD_LIDERAZGO);
          setNarrativeText(narrative.replace(/__BR__/g, "\n\n"));
          break;
        case TYPES.COMPARATIVA_EFECTIVIDAD:
          setData(COMPARATIVA_EFECTIVIDAD);
          setTextResult(
            `La persona tiene una efectividad mayor al ${score}% de los evaluados.`
          );
          break;
        case TYPES.RESISTENCIA_CAMBIO:
          setData(RESISTENCIA_CAMBIO);
          setNarrativeText(narrative.replace(/__BR__/g, "\n\n"));
          break;
        case TYPES.COMPARATIVA_RESISTENCIA:
          setData(COMPARATIVA_RESISTENCIA);
          setTextResult(
            `El porcentaje de personas con el mismo nivel de resistencia es: ${score}%.`
          );
          break;
        case TYPES.REPERTORIO_ESTILOS:
          const principalData = replaceTildes(narrative.principalData);
          const secondaryData = replaceTildes(narrative.secondaryData);
          const thirdData = replaceTildes(narrative.thirdData);
          const newNarrative = {
            principalData,
            secondaryData,
            thirdData,
          };
          REPERTORIO_ESTILOS.information = principalData;
          REPERTORIO_ESTILOS.secondaryInformation = fixNarrative(
            narrative.secondaryData
          );
          REPERTORIO_ESTILOS.thirdInformation = thirdData;
          setData(REPERTORIO_ESTILOS);
          break;
        case TYPES.BALANCE_ESTILOS:
          setData(BALANCE_ESTILOS);

          break;
        case TYPES.COMPARATIVA_REPERTORIO:
          setData(COMPARATIVA_REPERTORIO);
          setTextResult(
            `El porcentaje de personas con el mismo repertorio es: ${score}%.`
          );
          break;
        case TYPES.EFECTIVIDAD_PRESION:
          setData(EFECTIVIDAD_PRESION);
          setNarrativeText(narrative.replace(/__BR__/g, "\n\n"));
          break;
        case TYPES.COMPARATIVA_PRESION:
          setData(COMPARATIVA_PRESION);
          setTextResult(
            `La persona tiene una efectividad ante la presin mayor al ${score}% de los evaluados.`
          );
          break;
        case TYPES.NECESIDAD_ESTRUCTURACION:
          setData(NECESIDAD_ESTRUCTURACION);
          setNarrativeText(narrative.replace(/__BR__/g, "\n\n"));
          break;
        case TYPES.COMPARATIVA_ESTRUCTURACION:
          setData(COMPARATIVA_ESTRUCTURACION);
          setTextResult(
            `El porcentaje de personas con el mismo nivel de necesidad de estructuracin es: ${score}%.`
          );
          break;
        case TYPES.ADAPTABILIDAD:
          setData(ADAPTABILIDAD);
          setNarrativeText(narrative.replace(/__BR__/g, "\n\n"));
          break;
        case TYPES.COMPARATIVA_ADAPTABILIDAD:
          setData(COMPARATIVA_ADAPTABILIDAD);
          setTextResult(
            `La persona tiene una adaptabilidad mayor al ${score}% de los evaluados.`
          );
          break;
        case TYPES.COMPARATIVA_COMPETENCIAS:
          setData(COMPARATIVA_COMPETENCIAS);
          setTextResult(
            `La persona tiene una aptitud gerencial mayor al ${score}% de los evaluados.`
          );
          break;
        case TYPES.RELACION_SUPERIORES:
          setData(RELACION_SUPERIORES);
          setNarrativeText(narrative.replace(/__BR__/g, "\n\n"));
          break;
        case TYPES.RELACION_COLEGAS:
          setData(RELACION_COLEGAS);
          break;
        case TYPES.RELACION_EQUIPO:
          setData(RELACION_EQUIPO);
          break;
        case TYPES.RELACION_ASESORES:
          setData(RELACION_ASESORES);
          break;
        case TYPES.RELACION_GRUPOS:
          setData(RELACION_GRUPOS);
          break;
        case TYPES.RELACION_CLIENTES:
          setData(RELACION_CLIENTES);
          break;
        case TYPES.RELACION_PUBLICO:
          setData(RELACION_PUBLICO);
          break;
        case TYPES.CREATIVIDAD:
          setData(CREATIVIDAD);
          break;
        case TYPES.FIJACION_OBJETIVOS:
          setData(FIJACION_OBJETIVOS);
          break;
        case TYPES.PLANEACION:
          setData(PLANEACION);
          break;
        case TYPES.MANEJO_CAMBIO:
          setData(MANEJO_CAMBIO);
          break;
        case TYPES.IMPLEMENTACION:
          setData(IMPLEMENTACION);
          break;
        case TYPES.CONTROLES:
          setData(CONTROLES);
          break;
        case TYPES.EVALUACION:
          setData(EVALUACION);
          break;
        case TYPES.PRODUCTIVIDAD:
          setData(PRODUCTIVIDAD);
          break;
        case TYPES.COMUNICACION:
          setData(COMUNICACION);
          break;
        case TYPES.MANEJO_CONFLICTO:
          setData(MANEJO_CONFLICTO);
          break;
        case TYPES.MANEJO_ERRORES:
          setData(MANEJO_ERRORES);
          break;
        case TYPES.COMUNICACION_JUNTAS:
          setData(COMUNICACION_JUNTAS);
          break;
        case TYPES.TRABAJO_EQUIPO:
          setData(TRABAJO_EQUIPO);
          break;
        case TYPES.POTENCIAL_DESARROLLO:
          setData(POTENCIAL_DESARROLLO);
          fixNarrative(narrative);
          break;
        case TYPES.COMPARATIVA_POTENCIAL:
          setData(COMPARATIVA_POTENCIAL);
          setTextResult(
            `La persona tiene un potencial de desarrollo mayor al ${score}% de los evaluados.`
          );
          break;
        default:
          break;
      }
    }
  };
  useEffect(() => {
    if (data !== null) {
    }
  }, [data]);

  if (data === null) {
    return (
      <View style={styles.loadingContainer}>
        <Text>Cargando...</Text>
      </View>
    );
  }

  return (
    <View style={styles.containerAll}>
      <ScrollView contentContainerStyle={styles.containerAllInfo}>
        <Text style={styles.textTitle}>{data.title}</Text>
        <View style={styles.inlineTextContainer}>
          {type == TYPES.REPERTORIO_ESTILOS ? (
            <>
              <Text style={styles.textInfo}>{data.information}</Text>
            </>
          ) : (
            <></>
          )}
          {type == TYPES.ESTILO_PREFERENTE ||
          type == TYPES.COMPARATIVA_EFECTIVIDAD ||
          type == TYPES.COMPARATIVA_RESISTENCIA ||
          type == TYPES.COMPARATIVA_REPERTORIO ||
          type == TYPES.BALANCE_ESTILOS ||
          type == TYPES.COMPARATIVA_PRESION ||
          type == TYPES.COMPARATIVA_ESTRUCTURACION ||
          type == TYPES.COMPARATIVA_ADAPTABILIDAD ||
          type == TYPES.COMPARATIVA_COMPETENCIAS ||
          type == TYPES.COMPARATIVA_POTENCIAL ? (
            <>
              {data.information.map((item, index) => (
                <Text key={index} style={[styles.textInfo, item.style]}>
                  {item.text}
                </Text>
              ))}
              <Text style={[styles.textInfo, styles.textResult]}>
                {textResult}
              </Text>
            </>
          ) : (
            <>
              {type == TYPES.RELACION_SUPERIORES ||
              type == TYPES.RELACION_COLEGAS ||
              type == TYPES.RELACION_EQUIPO ||
              type == TYPES.RELACION_GRUPOS ||
              type == TYPES.RELACION_ASESORES ||
              type == TYPES.RELACION_CLIENTES ||
              type == TYPES.RELACION_PUBLICO ||
              type == TYPES.CREATIVIDAD ||
              type == TYPES.FIJACION_OBJETIVOS ||
              type == TYPES.PLANEACION ||
              type == TYPES.MANEJO_CAMBIO ||
              type == TYPES.IMPLEMENTACION ||
              type == TYPES.CONTROLES ||
              type == TYPES.EVALUACION ||
              type == TYPES.PRODUCTIVIDAD ||
              type == TYPES.COMUNICACION ||
              type == TYPES.MANEJO_CONFLICTO ||
              type == TYPES.MANEJO_ERRORES ||
              type == TYPES.COMUNICACION_JUNTAS ||
              type == TYPES.POTENCIAL_DESARROLLO ||
              compassNarrative ||
              type == TYPES.TRABAJO_EQUIPO ? (
                <Text style={styles.textInfo}>{data.information}</Text>
              ) : (
                <Text style={styles.textInfo}>{narrativeText}</Text>
              )}
            </>
          )}
        </View>
        {type == TYPES.ESTILO_PREFERENTE ||
        type == TYPES.REPERTORIO_ESTILOS ||
        type == TYPES.BALANCE_ESTILOS ||
        type == TYPES.POTENCIAL_DESARROLLO ? (
          <>
            <View style={styles.hr} />
            <Text style={styles.textTitle}>{data.secondaryTitle}</Text>
            {type == TYPES.POTENCIAL_DESARROLLO ||
            type == TYPES.BALANCE_ESTILOS ? (
              <>
                <Text style={{ letterSpacing: 0.7, marginBottom: 20 }}>
                  {data.subTitle}
                </Text>
              </>
            ) : (
              <></>
            )}
            {type == TYPES.ESTILO_PREFERENTE ||
            type == TYPES.REPERTORIO_ESTILOS ||
            type == TYPES.POTENCIAL_DESARROLLO
              ? list.secondaryInformation.map((item, index) => (
                  <View key={index} style={styles.inlineTextContainerWithIcon}>
                    <Icon name="circle-medium" size={20} color={"#415972"} />
                    <Text style={[item.style, { letterSpacing: 0.7 }, styles.textInfo]}>
                      {item.text}
                    </Text>
                  </View>
                ))
              : data.secondaryInformation.map((item, index) => (
                  <View key={index} style={styles.inlineTextContainerWithIcon}>
                    <Icon name="circle-medium" size={20} color={"#415972"} />
                    <Text style={[item.style, { letterSpacing: 0.7 }]}>
                      {item.text}
                    </Text>
                  </View>
                ))}
          </>
        ) : type == TYPES.RELACION_SUPERIORES ||
          type == TYPES.RELACION_COLEGAS ||
          type == TYPES.RELACION_EQUIPO ||
          type == TYPES.RELACION_GRUPOS ||
          type == TYPES.RELACION_ASESORES ||
          type == TYPES.RELACION_CLIENTES ||
          type == TYPES.RELACION_PUBLICO ||
          type == TYPES.CREATIVIDAD ||
          type == TYPES.FIJACION_OBJETIVOS ||
          type == TYPES.PLANEACION ||
          type == TYPES.MANEJO_CAMBIO ||
          type == TYPES.IMPLEMENTACION ||
          type == TYPES.CONTROLES ||
          type == TYPES.EVALUACION ||
          type == TYPES.PRODUCTIVIDAD ||
          type == TYPES.COMUNICACION ||
          type == TYPES.MANEJO_CONFLICTO ||
          type == TYPES.MANEJO_ERRORES ||
          type == TYPES.COMUNICACION_JUNTAS ||
          type == TYPES.TRABAJO_EQUIPO ? (
          <>
            <View style={styles.hr} />
            <Text style={styles.textTitle}>{data.secondaryTitle}</Text>
            <View style={styles.inlineTextContainerWithIcon}>
              <Text style={styles.textInfo}>{narrative}</Text>
            </View>
          </>
        ) : (
          <></>
        )}
        {type == TYPES.REPERTORIO_ESTILOS ? (
          <>
            <Text style={styles.textTitle}>{data.thirdTitle}</Text>
            <Text style={styles.textInfo}>{data.thirdInformation}</Text>
          </>
        ) : (
          <></>
        )}
      </ScrollView>
    </View>
  );
};

export default ViewMoreInfo;

const styles = StyleSheet.create({
  containerAll: {
    backgroundColor: "#fff",
    flex: 1,
    justifyContrn: 'center',
    alignItems: 'center',
  },
  containerAllInfo: {
    backgroundColor: "#fff",
    padding: 10,
    flexGrow: 1,
  },
  textResult: {
    fontWeight: "700",
  },
  textTitle: {
    color: "#F25455",
    fontSize: 14,
    fontWeight: "800",
    marginTop: 10,
    marginBottom: 10,
  },
  inlineTextContainer: {
    flexDirection: "row",
    flexWrap: "wrap",
  },
  textInfo: {
    letterSpacing: 0.7,
    fontSize: 13,
    color: "#2C3E50",
    fontWeight: "400",
  },
  inlineTextContainerWithIcon: {
    width: "95%",
    flexDirection: "row",
  },
  hr: {
    border: "none",
    borderTopWidth: 1,
    borderColor: "#D4D6DD",
    marginVertical: 20,
    width: "100%",
    height: 1,
    backgroundColor: "#D4D6DD",
    marginVertical: 20,
    alignSelf: "center",
  },
  loadingContainer: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
});
