import { StyleSheet, Text, View, TouchableOpacity } from "react-native";
import React from "react";
import Icon from "react-native-vector-icons/MaterialCommunityIcons";
import { AnimatedCircularProgress } from "react-native-circular-progress";
import { useNavigation } from "@react-navigation/native";

const CardGauge = (props) => {
  const { aboutCard, infoCard, points } = props
  const navigation = useNavigation();

  const goToViewInfo = () => {
    navigation.navigate("ViewMoreInfo", {
      headerTitleText: aboutCard,
      score: points,
    });
  };

  return (
    <View style={styles.containerAllGauge}>
      <View style={styles.containerAllCardGauge}>
        <View style={styles.containerCardGauge}>
          <View style={styles.containerHeadersGauge}>
            <Text style={styles.textGauge}>{infoCard}</Text>
            <View style={styles.containerInfoGauge}>
              <Text style={styles.textType}>+{points}%</Text>
            </View>
          </View>
          <View style={styles.containerGaugeAll}>
            <AnimatedCircularProgress
              size={280}
              width={55}
              fill={points}
              tintColor="#F25455"
              backgroundColor="#E8E9F1"
              rotation={-90}
              arcSweepAngle={180}
            ></AnimatedCircularProgress>
          </View>
          <View style={styles.containerButtonGauge}>
            <TouchableOpacity
              style={styles.buttonViewResultGauge}
              onPress={goToViewInfo}
            >
              <Icon name="plus" size={20} color={"#415972"} />
              <Text style={styles.textViewResultGauge}>Ms info</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    </View>
  );
};

