export const replaceTildes = (string) => {
  const specialCharacters = {
    "&#225;": "",
    "&#193;": "",
    "&#233;": "",
    "&#201;": "",
    "&#237;": "",
    "&#205;": "",
    "&#243;": "",
    "&#211;": "",
    "&#250;": "",
    "&#218;": "",
    "&#241;": "",
    "&#209;": "",
    "&aacute;": "",
    "&eacute;": "",
    "&iacute;": "",
    "&oacute;": "",
    "&uacute;": "",
    "&Aacute;": "",
    "&Eacute;": "",
    "&Iacute;": "",
    "&Oacute;": "",
    "&Uacute;": "",
    "&nbsp;": "",
    "&#252;": "",
    "&#220;": "",
    "&uuml;": "",
    "&iquest;": "",
    "&iexcl;": "",
    "&ntilde;": "",
  };

  for (const code in specialCharacters) {
    string = string.replace(new RegExp(code, "g"), specialCharacters[code]);
  }

  return string;
};

export const separateStatements = (string) => {
  const sentences = string.split(".").map((sentence) => {
    const formattedSentence = sentence.trim();
    if (formattedSentence.length > 0) {
      const firstChar = formattedSentence.charAt(0).toUpperCase();
      const restOfSentence = formattedSentence.slice(1).toLowerCase();
      return {
        text: firstChar + restOfSentence + ".\n",
      };
    }
    return null;
  });
  const filteredSentences = sentences.filter((sentence) => sentence !== null);
  return filteredSentences;
};

export const separateNeedStatus = (text) => {
  const sentences = text
    .split(".")
    .map((sentence) => sentence.replace(/-/g, "").trim());
  const parsedSentences = {
    information: [],
  };

  sentences.forEach((sentence, index) => {
    if (sentence.length > 0) {
      parsedSentences.information.push({
        id: index + 1,
        text: sentence,
      });
    }
  });

  return parsedSentences;
};

export const separateByCapitalLetters = (string) => {
  const cleanedText = string.replace(/\n/g, " ").replace(/\s+/g, " ").trim();
  let index = cleanedText.search(/[A-Z]/);

  if (index === -1) {
    return null;
  }

  const title = cleanedText.substring(0, index).trim();
  const text = cleanedText.substring(index).trim();

  const result = {
    title: title,
    text: text,
  };

  return result;
};

export const separateByColon = (string) => {
  const cleanedText = string.replace(/\n/g, " ").replace(/\s+/g, " ").trim();

  let index = cleanedText.search(/[A-Z]/);
  let colonIndex = cleanedText.indexOf(":", index);

  if (index === -1 || colonIndex === -1) {
    return null;
  }

  if (colonIndex > index && colonIndex - index < 100) {
    // Limiting separation to 100 characters
    index = colonIndex;
  }

  const title = cleanedText.substring(0, index + 1).trim(); // Include the colon in the title
  const text = cleanedText.substring(index + 1).trim();

  const result = {
    title: title,
    text: text,
  };

  return result;
};

export const cleanJsonCompass = (data) => {
  let cleanedData = JSON.stringify(data);

  // Reemplazar las etiquetas <li> por guiones -
  cleanedData = cleanedData.replace(/<li[^>]*>/g, "-");

  // Eliminar todas las etiquetas HTML restantes
  cleanedData = cleanedData.replace(/<\/?[^>]+(>|$)/g, "");

  // Reemplazar los guiones por la cadena deseada despus de limpiar las etiquetas HTML
  cleanedData = cleanedData.replace(/-/g, "__CIRCLE__");

  // Convertir el JSON limpio de nuevo a objeto
  cleanedData = JSON.parse(cleanedData);

  return cleanedData;
};
