import { Platform, StyleSheet, View, Text } from "react-native";
import React, { useState } from "react";
import { WebView } from "react-native-webview";
import { getUserCacheData } from "../../service/user/UserService";

const ViewPDF = () => {
  const [guid, setGuid] = useState({});
  getUserCacheData().then((data) => {
    setGuid(data.GUID_TEST);
  });

  return (
    <View style={styles.containerViewPDF}>
      <WebView
        source={{
          uri: `https://pwapdf.reddinassessments.com/DiagnosticoDEG3pwa?numtest=${guid}`,
        }}
        style={{ flex: 1 }}
      />
    </View>
  );
};

export default ViewPDF;

const styles = StyleSheet.create({
  containerViewPDF: {
    flex: 1,
    backgroundColor: "#fff",
  },
  loadingIndicator: {
    ...StyleSheet.absoluteFillObject,
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "rgba(255, 255, 255, 0.8)",
  },
  textInfo: {
    letterSpacing: 0.7,
    fontSize: 13,
    color: "#2C3E50",
    fontWeight: "400",
  },
  containerAndroid:{
    margin: 20,
    display: "flex",
    justifyContent:"center",
    alignItems:"center"
  }
});
