import AsyncStorage from "@react-native-async-storage/async-storage";
import { createURL } from "../../utils/enviroment";
import axios from "axios";
import { Alert } from "react-native";

export const LoginUser = async (email, password) => {
  let config = {
    method: "get",
    maxBodyLength: Infinity,
    url: createURL([`wsGetMyLoginInfo?pUserid=${email}&pPassword=${password}`]),
    headers: {
      Cookie: "SESSIONIDX=gih9luJT6hYGOP9oFmJDqWlPN47goF",
    },
  };
  try {
    const response = await axios.request(config);
    return response.data;
  } catch (error) {
    Alert.alert("Error al iniciar sesin", "Intntelo de nuevo ms tarde")
  }
};

export const getUserCacheData = async () => {
  try {
    const userDataJson = await AsyncStorage.getItem("userData");
    if (userDataJson) {
      const userData = JSON.parse(userDataJson);
      return userData;
    } else {
      Alert.alert("Error al conseguir los datos", "Intntelo de nuevo ms tarde")
    }
  } catch (error) {
    Alert.alert("Error al conseguir los datos", "Intntelo de nuevo ms tarde")
  }
};

export const getCredentialsCacheData = async () => {
  try {
    const credentials = await AsyncStorage.getItem("credentials");
    if (credentials) {
      const jsonCredentials = JSON.parse(credentials);
      return jsonCredentials;
    } else {
      Alert.alert("Error al conseguir los datos", "Intntelo de nuevo ms tarde")
    }
  } catch (error) {
    Alert.alert("Error al conseguir los datos", "Intntelo de nuevo ms tarde")
  }
};

export const changeCredentials = async(data) => {

  let config = {
    method: 'post',
    maxBodyLength: Infinity,
    url: createURL([`changelogin`]),
    headers: { 
      'Cookie': 'SESSIONIDX=rFudDKkrcp0PQInQ1QKNl1JBtrWvU4', 
      'Content-Type': 'application/json'
    },
    data : data
  };
  try {
    const response = await axios.request(config);
    return response.data;
  } catch (error) {
    Alert.alert("Error al inetntar cambiar las credenciales", "Intntelo de nuevo ms tarde")
  }
}
