import {
  Alert,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
  ScrollView,
  Modal,
} from "react-native";
import React, { useEffect, useState } from "react";
import { useNavigation, useRoute } from "@react-navigation/native";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { Camera } from "expo-camera";

const Compass = () => {
  const [compassData, setCompassData] = useState([]);
  const [compassNarrative, setCompassNarrative] = useState({});
  const [isVisible, setIsVisible] = useState(false);
  const route = useRoute();

  useEffect(() => {
    checkCompass();
  }, []);
  const checkCompass = async () => {
    try {
      const allKeys = await AsyncStorage.getAllKeys();
      const compassKeys = allKeys.filter((key) => key.startsWith("compass_"));

      const compassKeysWithTimestamp = await Promise.all(
        compassKeys.map(async (key) => {
          const compassObjectString = await AsyncStorage.getItem(key);
          if (compassObjectString) {
            const compassObject = JSON.parse(compassObjectString);
            return { key, timestamp: compassObject.timestamp };
          }
          return null;
        })
      );

      const sortedCompassKeys = compassKeysWithTimestamp
        .filter((item) => item !== null)
        .sort((a, b) => b.timestamp - a.timestamp)
        .map((item) => item.key);

      const compassDataArray = await Promise.all(
        sortedCompassKeys.map(async (key) => {
          const compassObjectString = await AsyncStorage.getItem(key);
          return compassObjectString ? JSON.parse(compassObjectString) : null;
        })
      );

      setCompassData(compassDataArray.filter((object) => object !== null));
    } catch (error) {
      Alert.alert(
        "Error al conseguir los datos",
        "Intntelo de nuevo ms tarde"
      );
    }
  };

  useEffect(() => {
    if (route.params && route.params.compassData && route.params.guidCompass) {
      const { guidCompass, compassData } = route.params;
      const timeStamp = new Date().getTime();
      const item = {
        guid: guidCompass,
        data: compassData,
        timestamp: timeStamp,
        key: `compass_${guidCompass}`,
      };
      AsyncStorage.setItem(`compass_${guidCompass}`, JSON.stringify(item));
      checkCompass();
    }
  }, [route.params]);

  const navigation = useNavigation();

  const closeModal = () => {
    setIsVisible(false);
  };

  const goToScanQR = async () => {
    setIsVisible(false);
    navigation.navigate("ScanQR");
  };

  const checkPermissions = async () => {
    try {
      const { status } = await Camera.requestCameraPermissionsAsync();
      if (status === "granted") {
        setTimeout(() => {
          goToScanQR();
        }, 500);
      } else {
        setIsVisible(true);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const goToCompassResults = (data) => {
    navigation.navigate("CompassResults", {
      compass: data,
    });
  };

  const handleDeleteCard = async (key) => {
    // Utilizar la clave especfica
    try {
      await AsyncStorage.removeItem(key);
      checkCompass();
    } catch (error) {
      Alert.alert("Error al eliminar el test", "Intntelo de nuevo ms tarde");
    }
  };

  const createCard = (compass, index) => {
    return (
      <View style={styles.containerCardCompass}>
        <View style={styles.containerCompassResult}>
          <View style={styles.containerButtonCompass}>
            <TouchableOpacity
              style={styles.trashIcon}
              onPress={() => handleDeleteCard(compass.key)}
            >
              <Text style={styles.deleteCard}>Eliminar</Text>
            </TouchableOpacity>
          </View>
          <Text style={styles.compassTitle}>NOMBRE:</Text>
          <Text style={styles.compassInfo}>{compass.data.NOMBRE}</Text>
          <Text style={styles.compassTitle}>FECHA:</Text>
          <Text style={styles.compassInfo}>{compass.data.FECHA}</Text>
          <Text style={styles.compassTitle}>PUESTO:</Text>
          <Text style={styles.compassInfo}>{compass.data.PUESTO}</Text>
          <View style={styles.containerCompassButton}>
            <TouchableOpacity
              style={styles.buttonCompass}
              onPress={() => goToCompassResults(compass.data)}
            >
              <Text style={styles.textButtonCompass}>Ver resultado</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  };

  return (
    <View style={styles.containerCompass}>
      <ScrollView contentContainerStyle={styles.scrollCompass}>
        <View style={styles.containerAllCompass}>
          <View style={styles.containerButtonCompass}>
            <TouchableOpacity
              onPress={checkPermissions}
              style={[styles.buttonCompass, { width: "90%" }]}
            >
              <Text style={styles.textButtonCompass}>Scan QR Code</Text>
            </TouchableOpacity>
          </View>
          {compassData.length !== 0 ? (
            compassData.map((item, index) => (
              <View key={index}>{createCard(item, index)}</View>
            ))
          ) : (
            <View style={styles.containerResultCompass}>
              <Text style={styles.textNotCompass}>
                No existen tests compass para comparar - Escanee un QR de alguno
                de sus compaeros
              </Text>
            </View>
          )}
        </View>
      </ScrollView>
      <Modal
        transparent={true}
        visible={isVisible}
        animationType="fade"
        onRequestClose={closeModal}
      >
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View style={styles.containerTitleModal}>
              <Text style={styles.titleModal}>Camera access</Text>
            </View>
            <Text style={styles.textModalLogout}>
              The Reddin Assessments application offers the possibility of
              comparing your results with those of a colleague, for this you
              need to scan a QR code. Allow the app to access the camera in the
              next step.
            </Text>
            <View style={styles.modalButtons}>
              <TouchableOpacity
                style={styles.modalButtonLogout}
                onPress={goToScanQR}
              >
                <Text style={styles.textLogout}>Next</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </Modal>
    </View>
  );
};

